/*.myBord {*/
/*position: relative;*/
/*width: 100px;*/
/*height: 100px;*/
/*margin: 20px;*/
/*border: 1px solid rgb(252,189,20);*/
/*}*/
.myBord:before {
    display: block;
    content: "";
    width: 30px;
    height: 30px;
    position: absolute;
    top: -10px;
    left: -10px;
    border-top: 1px solid rgb(252,189,20);
    border-left: 1px solid rgb(252,189,20);
}
.myBord:after {
    display: block;
    content: "";
    width: 30px;
    height: 30px;
    position: absolute;
    top: -10px;
    right: -10px;
    border-top: 1px solid rgb(252,189,20);
    border-right: 1px solid rgb(252,189,20);
}
.myBorderDetail:before {
    display: block;
    content: "";
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: -10px;
    left: -10px;
    border-bottom: 1px solid rgb(252,189,20);
    border-left: 1px solid rgb(252,189,20);
}
.myBorderDetail:after {
    display: block;
    content: "";
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: -10px;
    right: -10px;
    border-bottom: 1px solid rgb(252,189,20);
    border-right: 1px solid rgb(252,189,20);
}

